body{
    width: 100%;
    margin: 0 auto;
}
h1, h2, h3, h4, h5, h5, figure, p{margin: 0;}
a{text-decoration: none; margin: 0; padding: 0;}
ul, li{list-style-type: none; margin: 0; padding: 0;}
img{vertical-align: middle;}
table{border-collapse: collapse;}
.intropage{
    width: 100%;
    position: relative;
}
.intropage .fadein2{
    width: 90%;
    font-family: 'Gothic A1';
    font-size: 150px;
    font-weight: normal;
    letter-spacing: -5px;
    color: rgb(0, 255, 64);
    margin: 50px auto;
    -webkit-animation: fadein 3s ease-in-out;
            animation: fadein 3s ease-in-out;
}
.intropage .fadein{
    width: 90%;
    font-family: 'Aeonik TRIAL';
    font-size: 200px;
    font-weight: normal;
    letter-spacing: -5px;
    line-height: 80%;
    color: rgb(0, 119, 255);
    margin: 50px auto;
    -webkit-animation: fadein 3s ease-in-out;
            animation: fadein 3s ease-in-out;
}
@-webkit-keyframes fadein{
    0% {
        opacity: 0;
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
    }
    100% {
        opacity: 1;
        -webkit-transform: none;
                transform: none;
    }
}
@keyframes fadein{
    0% {
        opacity: 0;
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
    }
    100% {
        opacity: 1;
        -webkit-transform: none;
                transform: none;
    }
}
